.flex-row.form-row {
  @include max_tablet {
    display: block;
  }
}
.hg-form-component-input {
  margin: 0 auto;
  padding:0 5px;
  margin-bottom: 20px;
  .input-group.date {
    border-bottom: 2px solid #E5E5E5;


  }
  .input-group.date:hover {
    border-bottom: 2px solid black;

  }
  .input-group.date:after {
    position: absolute;
    content: '\f073';
    color: #aaa;
    font: normal normal normal 14px/1 FontAwesome;
    right: 22px;
    top: 12px;
    @media (min-width: 768px) and (max-width: 992px){
          top: 17px;
          right: 9px;
    }
  }
  .input-group {
    height: 50px;
    display: inline-flex;
    width: 100%;
    input[type=text] {
      @extend select;
      border-bottom: 2px solid #E5E5E5;
      border-radius: 0;
    }
  }

  label {
    display: block;
    color: #222;
    text-transform: uppercase;
    line-height: 1;
    margin-bottom: 10px;
    font-size: 13px;
    position: relative;
  }
}
.hg-form-component {
  margin: 0 auto;
  padding:0 5px;
  margin-bottom: 20px;
  //responsiveness
  @include max_mobile {
    margin-top:20px;
  }
}
select {
 text-overflow: ellipsis;
 font-family: sans-serif;
 background: none;
 width: 100%;
 height: 50px;
 border: 0px;
 border-bottom: 2px solid $border;
 min-width: 0;
 padding: 7px 8px;
 //margin-bottom: 15px;
 font-size: 15px;
 color: $selectColor;
 line-height: 24px;
 box-shadow: inset 0 0 0 rgba(0,0,0,.075);
 transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
}
select:focus, select:hover {
   border-bottom: 2px solid #222;
   border-radius: 0px;
   outline: none;
 }
select:visited, select:active {
   outline: none;
 }
 .btn-gradient {
    font-family: $btnFont;
   	background: linear-gradient(to right,#7f82f0 0%,#5ad2d4 100%);
   	box-shadow: 0px 11px 27px 0px rgba(154,49,188,0.33);
   	color: #FFF;
   	font-weight: 500;
   	letter-spacing: 0.2px;
   	text-align: center;
    width: 100%;
    padding: 0px 28px;
    @media screen and (min-width:991px) and (max-width:1366px) {
      padding: 0px 0px;
    }
 }
 .btn-gradient:hover {
   opacity: 0.8;

 }
.search-container {
  h2 {
    margin-bottom: 10px;
    font-size: 16px;
    padding-top: 10px;
}}
.form-container {
      display: block;
      position: relative;
      width: 100%;
      min-height: 0px;
    //  margin-top: -49px;
      background-color: #ffffff;
      padding:0px 35px 35px 35px;
      box-shadow: 0 15px 75px 0 rgba(0,0,0,.06);

}
