  margin-top: -130px;
}
/* date picker style */
.ui-datepicker.ui-widget {
    z-index: 9999;
    display: none;
    position: relative;
    color: #333;
    background: #fff;
    border: 1px solid #ccc;
    box-shadow: 0 5px 15px -5px rgba(0,0,0,.5);
    border-radius: 0;
    padding: 8px;
}

.ui-datepicker.ui-widget .ui-datepicker-header {
    background: 0 0;
    border: 0
}

.ui-datepicker.ui-widget .ui-datepicker-header .ui-datepicker-next,.ui-datepicker.ui-widget .ui-datepicker-header .ui-datepicker-prev {
    background: center center no-repeat;
    background-size: 75% 75%;
    border: 0;
    cursor: pointer;
    width: 20px;
    height: 30px;
    opacity: .5;
    filter: alpha(opacity=50);
    transition: all .3s;
}

.ui-datepicker.ui-widget .ui-datepicker-header .ui-datepicker-next span,.ui-datepicker.ui-widget .ui-datepicker-header .ui-datepicker-prev span {
    display: none;
}

.ui-datepicker.ui-widget .ui-datepicker-header .ui-datepicker-next:hover,.ui-datepicker.ui-widget .ui-datepicker-header .ui-datepicker-prev:hover {
    opacity: 1;
    filter: alpha(opacity=100);
}

.ui-datepicker.ui-widget .ui-datepicker-header .ui-datepicker-prev {
    float: left;
    left: 0;
    right: auto;
    top: 4px;
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAeCAYAAAAsEj5rAAAAUklEQVR42u3VMQoAIBADQf8Pgj+OD9hG2CtONJB2ymQkKe0HbwAP0xucDiQWARITIDEBEnMgMQ8S8+AqBIl6kKgHiXqQqAeJepBo/z38J/U0uAHlaBkBl9I4GwAAAABJRU5ErkJggg==);
}

.ui-datepicker.ui-widget .ui-datepicker-header .ui-datepicker-next {
    background-image: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABQAAAAeCAYAAAAsEj5rAAAAU0lEQVR42u3VOwoAMAgE0dwfAnNjU26bYkBCFGwfiL9VVWoO+BJ4Gf3gtsEKKoFBNTCoCAYVwaAiGNQGMUHMkjGbgjk2mIONuXo0nC8XnCf1JXgArVIZAQh5TKYAAAAASUVORK5CYII=);
    float: right;
    right: 0;
    top: 4px;
    left: auto;
}

.ui-datepicker.ui-widget .ui-datepicker-header .ui-datepicker-title {
    color: #2a2a2a;
    padding: 5px 3px;
    font-size: 14px;
    line-height: 20px;
    font-weight: 700;
    span {
      color: #2a2a2a;
    }
}

.ui-datepicker.ui-widget .ui-datepicker-calendar {
    border: 0;
    margin-bottom: 24px;
}

.ui-datepicker.ui-widget .ui-datepicker-calendar th {
    color: #999;
    font-size: 12px;
    line-height: 25px;
    font-weight: 700;
    text-align: center;
}

.ui-datepicker.ui-widget .ui-datepicker-calendar th span {
    cursor: help;
    border-bottom: 1px dotted #777;
    color: #999;
}

.ui-datepicker.ui-widget .ui-datepicker-calendar td {
    padding: 0;
    border: 0;
    height: 27px;
    overflow: hidden;
}

.ui-datepicker.ui-widget .ui-datepicker-calendar td.ui-state-disabled {
    padding: 0;
    border: 0;
}

.ui-datepicker.ui-widget .ui-datepicker-calendar td.ui-state-disabled .ui-state-default:hover {
    background: #f5f5f5;
    color: #666;
}

.ui-datepicker.ui-widget .ui-datepicker-calendar .ui-state-default {
    cursor: pointer;
    display: block;
    outline: 0;
    border: 0;
    margin: 0;
    width: 100%;
    padding: 5px;
    color: #666;
    font-size: 12px;
    line-height: 17px;
    text-align: right;
    background: #f5f5f5;
}

.ui-datepicker.ui-widget .ui-datepicker-calendar .ui-state-default:hover {
    color: #fff;
    background: #ffb606;
}
